import React, { useState } from 'react';
import PropTypes from 'prop-types'
import { Button, Row, Col, Form } from 'react-bootstrap'
import categories from '../categories'

function Search({
  changeSelectedCat,
  changeSelectedPost
}) {
  const [cat, setCat] = useState(1)
  const [postcode, setPostcode] = useState()
  const [validated, setValidated] = useState(false);

  const handleSubmit = event => {
    const form = event.currentTarget;
    event.preventDefault()
    event.stopPropagation()
    if (form.checkValidity() === true) {
      changeSelectedCat(cat)
      changeSelectedPost(postcode)
    }
    setValidated(true)
  };

  const onChangeCat = (evt) => {
    setCat(evt.target.value)
  }

  const onChangePost = (evt) => {
    setPostcode(evt.target.value)
  }

  return (
    <Form noValidate validated={validated} onSubmit={handleSubmit}>
      <Row>
      <Col sm={4}>
        <Form.Group controlId="exampleForm.ControlSelect1">
          <Form.Label>Category</Form.Label>
          <Form.Control as="select" onChange={onChangeCat} >
          {
            categories.map(
              category =>
                // I assume that if the category is hidden we should not show it in the SELECT field
                !category.hidden &&
                <option key={category.id} value={category.id}>{category.name}</option>)
          }
          </Form.Control>
        </Form.Group>
      </Col>
      <Col sm={4}>
        <Form.Group controlId="formGridAddress1">
          <Form.Label>Postcode</Form.Label>
          <Form.Control placeholder="SW15 6EW" onChange={onChangePost} required/>
        </Form.Group>
      </Col>
      <Col sm={4} className='padding-submit'>
        <Button role="submit" variant="primary" type="submit">
          Submit
        </Button>
      </Col>
    </Row>
    </Form>
  );
}

Search.propTypes = {
  changeSelectedCat: PropTypes.func.isRequired,
  changeSelectedPost: PropTypes.func.isRequired
}

export default Search;
