import React, { useEffect, useState } from 'react'
import PropTypes from 'prop-types'
import axios from 'axios'
import { Table } from 'react-bootstrap'
import ReactPaginate from 'react-paginate'
import TableRow from './tableRow'

function ProsTable({
  category,
  postcode
}) {
  const limit = 20
  const [pros, setPros] = useState([])
  const [totalPages, setTotalPages] = useState(0)
  const [currentPage, setCurrentPage] = useState(0)

  const handlePageClick = data => {
    setCurrentPage(data.selected)
  }

  useEffect(() => {
    // This endpoint call should be in a proper API wrapper, I didn't do it because it is only one call
    const fetchPros = async () => {
      try {
        const response = await axios.post('https://demo.plentific.com/find-a-pro/api/v2/public/pro/search-pros/', {
        category_id: category,
        location: postcode.replace(/\s/g, '').toLowerCase() // Removing spaces and caps
        },
        {
          headers: {
            "x-pagination-limit": limit,
            "x-pagination-offset": currentPage * limit
          }
        })
        if(response.status === 200) {
          // Setting number of pages and list of pros in current page
          setTotalPages(Math.ceil(response.headers['x-pagination-count'] / limit))
          setPros(response.data.response.pros)
        }
      } catch {
        // Remove previous results when ERROR
        setTotalPages(0)
        setPros([])
      }
    }

    if(postcode) {
      fetchPros()
    }
  }, [category, currentPage, postcode])

  return (
    <div className='text-center'>
      <Table responsive size="sm">
        <thead>
          <tr>
            <th>ID</th>
            <th>Name</th>
            <th>Postcode</th>
            <th>Review Rating</th>
          </tr>
        </thead>
        <tbody>
          {
            pros.map(
              pro =>
                <TableRow
                  key={pro.id}
                  pro={pro}
                />
            )
          }
        </tbody>
      </Table>
      {
        totalPages > 1 &&
        <ReactPaginate
          previousLabel={'previous'}
          nextLabel={'next'}
          breakLabel={'...'}
          pageCount={totalPages}
          marginPagesDisplayed={2}
          pageRangeDisplayed={5}
          onPageChange={handlePageClick}
          // styles from here
          containerClassName={'pagination'}
          subContainerClassName={'pages pagination'}
          activeClassName={'active'}
          breakClassName={'page-item'}
          breakLinkClassName={'page-link'}
          pageClassName={'page-item'}
          pageLinkClassName={'page-link'}
          previousClassName={'page-item'}
          previousLinkClassName={'page-link'}
          nextClassName={'page-item'}
          nextLinkClassName={'page-link'}
        />
      }
    </div>
  )
}

ProsTable.propTypes = {
  category: PropTypes.number.isRequired,
  postcode: PropTypes.string
}

export default ProsTable